@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  flex-direction: column;
  width: 200px;
  padding: 17px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  padding: 16px 0 0 3px;
}

.card__name {
  color: #060b35;
  text-align: justify;
  margin: 0;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 0;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  align-items: center;
  height: 16px;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
  display: flex;
}

.card__price--container {
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
  display: flex;
}

.card__price--title {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price--number {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  box-sizing: border-box;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: block;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

